export default {"name":"野针王","img":"\\lvcha\\yezhenwang\\img_20191122100457h59wxn.jpg","desc":["野针王是二十世纪七十年代，湖南农大著名茶叶专家朱先明教授在湘西武陵山脉调查湖南茶业资源时发现的一种野生茶树品种。"],"menus":[{"name":"野针王茶叶特点","content":[{"text":"野针王外形芽头肥壮，形似银针绿旗枪。野针王是高档\n        绿茶\n        ，汤色碧绿，黄汤黄叶。野针王“色绿、香高、味浓、形美”四者全拥有，这是中国高档名优绿茶所罕见的。用透明玻璃杯冲泡后，茶芽似群笋破土，亭亭玉立于杯中，茶香袭人，更为奇特的是：吸水后开始慢慢膨胀的“野针王”，似“仙女下凡”，在杯中慢舞，使人赏心悦目、心旷神怡，又似杨柳吐新，春染杯底，给人以春意盎然之感。"},{"src":"\\lvcha\\yezhenwang\\img_20191122100457nm6eh3.jpg"}]},{"name":"野针王加工工艺","content":[{"text":"1、采摘："},{"text":"要事先在春茶前期，选择壮龄的政大茶园，于清明后采其肥壮的1芽2、3叶嫩梢(不采对夹叶)，切忌损伤芽叶，尽量保持完整。"},{"src":"\\lvcha\\yezhenwang\\img_201911221004571elzfp.jpg"},{"text":"2、萎凋："},{"text":"采回鲜芽叶在微弱阳光下进行复式萎凋或直接用室内萎凋，薄摊于竹席或水筛上，历时很长，需16～18小时，使芽叶中多酚类进行部分酶性氧化，除去苦涩味，萎凋至水分失重25%～30%之时，叶色转暗绿，梗皮皱缩，白毫显露，毫毛如银，叶质柔软如棉，梗折不脆断为适度，过度易红变，不足则叶多青绿，失去传统风格，影响质量。"},{"text":"3、杀青："},{"text":"杀青锅温掌握在140～150度，先扬炒后闷炒，炒至叶色转青，梗带青黄，手握芽叶柔软，散发青香，无青臭味为适度，即可起锅。"},{"text":"4、揉捻："},{"text":"杀青叶摊凉散热后进行轻揉，与一般揉捻稍异，是揉搓和茶团旋转相结合，操作轻巧，注意保毫，并结合解决做形，搓揉至条索卷曲，历时约5～8分钟，揉好的湿茶团，每团约50～500克。"},{"text":"5、初烘："},{"text":"每焙笼可焙小茶团10～15个，将小茶团排列在焙笼中，温度100～110度，焙至五六成干，以茶条不粘手时为适度。"},{"text":"6、复焙整形："},{"text":"复焙刚开始时，温度掌握在50～60度，边焙边做形，使全部茶条卷曲似小猴。复焙温度不宜过高，以防止芽毫脱落，但也不宜过低，太低水分蒸发太慢，色泽闷黄不鲜，复焙到八九成干时，火温降至40～50度，一直焙至足干。最后经拣剔梗片，即可装箱。"}]},{"name":"野针王储藏方法","content":[{"text":"1、冷藏法"},{"text":"用冰箱冷藏茶叶，可以收到令人满意的效果。但有两点必须注意：一是要防止冰箱中的其他食品的气味(如鱼腥味)污染茶叶;二是茶叶本身必须是干燥的。"},{"text":"2、袋藏法"},{"text":"目前用得最多的是用塑料袋保藏茶叶，这也是家庭贮藏茶叶最简便、最经济的方法之一。用塑料袋包装茶叶能否起到有效的保藏作用，关键是一要茶叶本身干燥，二要选择好包装材料。"},{"text":"3、罐贮法"},{"text":"罐藏法比较简单，只要将从市场上买回的茶叶放入洁净的罐内即可。不过，为了更好地保持罐内干燥，可以在罐内放入1-2小包硅胶或保鲜剂，这样效果更好。"}]},{"name":"野针王冲泡方法","content":[{"text":"1、投茶"},{"text":"冲泡野针王时最宜选择晶莹剔透的玻璃杯，一来可以观赏野针王在水中翩翩起舞。二来可以欣赏其碧绿的汤色。冲泡野针王时投茶量为4克左右，喜欢清淡的投茶量稍少，喜欢味浓的可投稍多量，视个人喜好而定。投茶的方法选择下投法或中投法。"},{"src":"\\lvcha\\yezhenwang\\img_20191122100457r0lgvb.jpg"},{"text":"2、润茶"},{"text":"为使野针王的内含物和芳香物质更好地得以体现，在冲泡之前先用少量的水来浸润茶叶，冲水量覆盖过茶叶即可。润茶时间控制在30秒以内。"},{"text":"3、闻香"},{"text":"野针王制作采用怡清源独创的微波提香和炭火焙香相结合的技术。经过浸润的茶叶，芳香物质顿时伴随着水蒸气从茶杯中开始挥发，此时嗅闻茶叶香气，幽幽清香若兰扑鼻。"},{"text":"4、冲泡"},{"text":"闻香后再用开水冲泡，茶水比例一般为1：50左右，水温控制在85度左右为宜，饮水机中水烧开即可。因为水温过高会烫熟细嫩的茶叶，使其失去鲜嫩色泽和清鲜滋味。"},{"text":"5、赏茶"},{"text":"一斤野针王是精选4万个芽头制作而成，观赏着在水中翩翩起舞的野针王，仿似欣赏着亭亭玉立的佳人，令人心动不已。碧绿的汤色让你如沐春风，使人怡然自乐、心旷神怡。"},{"text":"6、品茗"},{"text":"野针王中茶氨酸、茶多酚、儿茶素、维生素等内含物比普通品种高出50%以上。细品野针王，其味厚浓冽，甘醇长久。作家王跃文在品饮野针王后评价她深重绵长，沉稳醇厚，雍容大气。袁隆平院士更是题书“野针王——形赛银针、香斗龙井、味胜碧螺”。在品饮至茶汤还剩1/3时续水，可冲泡数次。"}]},{"name":"野针王功效作用","content":[{"text":"【野针王补充维生素，提高抵抗力和免疫力】："},{"text":"野针王中含有维生素及其丰富，其中水溶性维生素(包括维生素C和B族维生素)，可以通过饮用直接被人体吸收利用。因此，常喝野针王是补充水溶性维生素的好方法，可以补充人体对多种维生素，尤其是维生素C的需要。每100克野针王中维生素C含量可高达350毫克，比柠檬、柑橘等水果含量还高。\n        红茶\n        、\n        乌龙茶\n        因加工中经发酵工序，维生素C受到氧化破坏而含量下降，每100克茶叶只剩几十毫克，尤其是红茶，含量更低。每人每日只要喝5克野针王，就能满足人体对维生素C的日需要量。"},{"text":"【野针王补充蛋白质和氨基酸，补充人体营养】："},{"text":"野针王中水溶性蛋白质比一般\n        绿茶\n        高出90%以上，喝野针王能直接吸收利用的水溶性蛋白质含量约为3.6%。此外，野针王中的氨基酸种类丰富，多达25种以上，其中的异亮氨酸、亮氨酸、赖氨酸、苯丙氨酸、苏氨酸、缬氨酸，是人体必需的八种氨基酸中的六种。茶氨酸占干茶中的2%以上，它具有解消疲劳、降低血压和提高学习记忆能力的作用。"},{"text":"【绿茶可以补充需要的矿物质元素，提高人体抗病能力】："},{"text":"野针王中含有很多人体所需的大量元素和微量元素。目前已定量检出的有30余种，14种人体所必需的微量元素。微量元素不能在体内合成，也不能在体内贮存，它必须随着能量的消耗，随时补充，如果摄入量不足，会使人生病，或缩短寿命，完全缺乏甚至能导致死亡。野针王生长在野生的环境中，富含多种矿质元素，它不仅含有全部人体所需的微量元素，而且有的元素含量较多，并有50%～60%可溶于热水，喝野针王就能直接从中获得。如果每天喝三杯野针王约10g茶，可从中获取相当于日需量12%～30%的矿质元素，个别茶叶所含的矿质元素甚至可以满足日需量。这无疑对人体有较好的保健作用。其次，野针王中的矿质元素，大多以有机态存在，有利于人体吸收。无机态存在的矿质元素对人体吸收利用并不理想，有的甚至还有一定的毒性。所以，喝野针王对发挥矿质元素的营养作用，是比较适宜的。"},{"text":"【野针王中高茶多酚含量，具有强大的保健功能】："},{"text":"野针王中所含茶多酚含量超出一般茶叶近两倍，是茶叶中最重要的保健功能性物质，茶多酚具有很强的消除有害自由基、抗衰老、抗辐射、抑制癌细胞、抗菌、杀菌、抑制艾滋病病毒的作用。"}]},{"name":"野针王获奖情况","content":[{"text":"1、“野针王”为注册商标。"},{"text":"2、“怡清源野针王”曾荣获中国湖南第四届(国际)农博会金奖，上海(中国)精品名茶博览会茶叶评比金奖。并且通过湖南省科技厅科技成果鉴定(证书编号：湘科成登字第943Y20030297号)。"},{"text":"3、“怡清源毛尖茶”三个产品符合绿色食品A级标准。"},{"text":"4、在第五届全国城市运动会上，怡清源野针茶系列产品再次以其独特的品质被定为城运会推荐用茶。"},{"text":"5、中国湖南第五届(国际)农博会指定产品。"},{"text":"6、连续三届荣获北京国际茶业博览会金奖。"},{"text":"7、2007年作为“国礼茶”胡锦涛总书记送给俄罗斯时任总统普京。"}]}]}